import React, { useState } from 'react'

import Navbar from "./Components/Navbar/Navbar";
import Textform from './Components/Textform/Textform';
import Alert from './Components/Alert/Alert';


function App() {

  // for themes
  const [mode, setMode] = useState('light');    // whether dark mode is enabled or not.

  // runs when toggle switch is clicked
  const toggleMode = () => {
    if (mode == 'light') {
      setMode('dark');
      document.body.style.backgroundColor = '#2d3339';
      document.body.style.color = 'white';
      showAlert("Dark mode has been enabled", "success");
    }
    else {
      setMode('light');
      document.body.style.backgroundColor = 'white';
      document.body.style.color = '#2d3339';
      showAlert("Light mode has been enabled", "success");
    }
  };




  // for alert box
  const [alert, setAlert] = useState(null);

  const showAlert = (message, type) => {
    setAlert({
      msg: message,
      type: type
    });

    // dismiss the alert box after 2s
    setTimeout(function() {
      setAlert(null);
    }, 2100);
  };





  return (
    <div className="App">
    
      <Navbar title="TextAnalyzer" mode={mode} toggleMode={toggleMode}/>

      <Alert alert={alert}/>


      <div className="container my-5">
        <Textform heading='Enter the text to be manipulated - Try It!' showAlert={showAlert}/>
      </div>

    </div>
  );
}

export default App;
