import './Textform.css';
import React, {useState} from 'react'

function Textform(props) {

     const [text, setText] = useState('');
     

     const handleOnChange = (event) => {
          setText(event.target.value);
     };


     const upperClick = (event) => {
          setText(text.toUpperCase());
          props.showAlert("Converted to upper case", "success");
     };

     const lowerClick = (event) => {
          setText(text.toLowerCase());
          props.showAlert("Converted to lower case", "success");
     };
     
     const clearClick = () => {
          setText('');
          props.showAlert("Cleared the text", "success");
     };
     
     // from solutions
     const copyText = () => {
          navigator.clipboard.writeText(text);
          props.showAlert("Text copied to clipboard", "success");
     };
     const removeExtraSpaces = () => {
          let newText = text.split(/[ ]+/);
          setText(newText.join(" "));
          props.showAlert("Extra spaces removed", "success");
     };



     return (
     <>
     <div className='container'>
          <h4>{props.heading}</h4>
          <div className="mb-3">
               <textarea className="form-control" id="myBox" rows="10"
                    value={text} onChange={handleOnChange} placeholder="Enter text here.."
                    style={{
                         backgroundColor: "#f2f2f2"
                    }}
               >
               </textarea>
          </div>

          <button type="button" disabled={text.length==0} className="btn btn-primary mx-2 my-2" onClick={upperClick}>Convert to UPPERCASE</button>
          <button type="button" disabled={text.length==0} className="btn btn-primary mx-2 my-2" onClick={lowerClick}>Convert to lowercase</button>
          
          <button type="button" disabled={text.length==0} className="btn btn-primary mx-2 my-2" onClick={removeExtraSpaces}>Remove Extra Spaces</button>
          <button type="button" disabled={text.length==0} className="btn btn-secondary float-end mx-2 my-2" onClick={copyText}>Copy Text</button>

          <button type="button" disabled={text.length==0} className="btn btn-dark float-end mx-2 my-2" onClick={clearClick}>Clear text</button>
     </div>

     <br /> <hr />

     <div className="container my-4">
          <h4>Your text summary</h4>
          {/* regex expression: split by (\s+) more than one whitespace chars and a new line*/}
          <p>{text.split(/\s+/).filter((element) => {
               return element.length!=0;
          }).length} words, {text.length} characters</p>

          {/* info based on google */}
          <p>An average reader will take about {0.008 * text.split(" ").filter((element) => {
               return element.length!=0;
          }).length} minutes to read this piece of text.</p>

          <br />

          <div className="preview">
               <h4>Preview</h4>
               <p>{text.length>0? text: "-- Nothing to preview! --"}</p>
          </div>
     </div>
     </>
     )
}

export default Textform
