import React from 'react'
import './Alert.css';

function Alert(props) {

     const capitalize = (word) => {
          const lower = word.toLowerCase();
          return lower.charAt(0).toUpperCase() + lower.slice(1);
     };


     return (
     // returns iff props.alert is true
     props.alert && <div className={`container alert alert-${props.alert.type} alert-dismissible fade show my-4`} role="alert">
          <strong>{capitalize(props.alert.type)}!</strong> &nbsp; {props.alert.msg}
     </div>
     )
}

export default Alert
