import React from 'react'

function Navbar(props) {
     return (
     <div className='app__navbar'>
          
          <nav className={`navbar navbar-expand-lg navbar-${props.mode} bg-${props.mode}`} style={{borderBottom: "1px solid #ccccff"}}>
               <div className="container-fluid">
                    <a className="navbar-brand" href="#">{props.title}</a>
                    <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                         <span className="navbar-toggler-icon"></span>
                    </button>
                    <div className="collapse navbar-collapse" id="navbarSupportedContent">
                         <ul className="navbar-nav me-auto mb-2 mb-lg-0">
                              <li className="nav-item">
                                   <a className="nav-link active" aria-current="page" href="#">Home</a>
                              </li>
                         </ul>

                         {/* dark theme */}
                         <div className={`form-check form-switch float-end text-${props.mode=='dark'?'light': 'dark'}`}>
                              <input className="form-check-input" type="checkbox" id="flexSwitchCheckDefaultDark" onClick={props.toggleMode}/>
                              <label className="form-check-label" htmlFor="flexSwitchCheckDefaultDark">Toggle theme</label>
                         </div>
                    </div>
               </div>
          </nav>

     </div>
     );
}

export default Navbar
